document.querySelectorAll(".no-spinners").forEach(function(time){
  time.addEventListener('focusout',function(){
    console.log(time.value.length);
    if(time.value.length == 1){
      time.value = "0"+time.value;
    }
  })
})


document.querySelectorAll(".no-spinners").forEach(function(time){
  time.addEventListener('change',function(){
    if(time.value < 9){
      time.value = "0"+time.value;
    }
  })
})
